import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService, Message } from 'primeng/primeng';
import { DiffererentiationBean } from './bean/differerentiation.bean';
import { Router } from '@angular/router';
// import { IsRcord, IsDiffWay } from './../constant/codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { PaginatorModule } from 'primeng/primeng';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
@Component({
  selector: 'app-differerentiation',
  templateUrl: './differerentiation.component.html',
  styleUrls: ['./differerentiation.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class Differerentiation implements OnInit {
  differentiationBean: DiffererentiationBean = new DiffererentiationBean();
  date3: any;
  ch: any = CALENDAR_CN;
  cars: any = [];
  aa: any;
  //table数据
  list: any = [];
  list1: any = [];
  list2: any = [];
  list3: any = [];
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //提示信息
  msgs: Message[] = [];
  effDt: Date;
  invalDt: Date;
  invalDtStart: Date;
  invalDtEnd: Date;
  prefSettgWayCode: any;
  //传入的值
  // @Input() inValue: string;
  //出参
  // @Output() outValue: EventEmitter<any> = new EventEmitter();
  isRcord: any = [];
  isDiffWay: any = [];
  total: number;
  first: number = 0;//分页控制
  tabMesg: any;//表格中无数据时显示的内容
  code: any;//获取所有码值
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc) {
    this.codeValues();
    this.isDiffWay = this.code['IsDiffWay'];
    this.isRcord = this.code['IsRcord'];
  }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.differentiationBean.pageSize = 10;
    this.differentiationBean.pageNum = 1;
    this.httpService.queryRatePlanInternalListService({ intRatePlnNum: "", intRatePlnName: "" }).subscribe(data => {
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "intRatePlnName", "intRatePlnNum")
      }
    });
    this.effDt = null;
    this.invalDt = null;
    this.differentiationBean.prefSettgWayCode = null;
    this.differentiationBean.prefIntRatePln = null;
    this.differentiationBean.rcrdStCode = null;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  querySecle() {
    this.differentiationBean.pageSize = 10;
    this.differentiationBean.pageNum = 1;
    this.first = 0;
    this.toquery();
  }
  toquery() {
    this.differentiationBean.effDt = this.commfunc.transDateN(this.effDt);//生效日期
    this.differentiationBean.invalDt = this.commfunc.transDateN(this.invalDt);
    this.differentiationBean.invalDtStart = this.commfunc.transDateN(this.invalDtStart);
    this.differentiationBean.invalDtEnd = this.commfunc.transDateN(this.invalDtEnd);
    this.httpService.queryRateDifferentRuleInternalService(this.differentiationBean).subscribe(data => {
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }
      this.list = data.resultList
      this.total = data.total;

      // 错误条件查询的提示
      if (data.returnCode.code != "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, err => {
      this.tabMesg = tableMessage
    })
  }
  //重置
  newSet() {
    this.differentiationBean = new DiffererentiationBean();
    this.effDt = null;
    this.invalDt = null;
    this.invalDtStart = null;
    this.invalDtEnd = null;
    this.differentiationBean.prefSettgWayCode = null;
    this.differentiationBean.prefIntRatePln = null;
    this.differentiationBean.rcrdStCode = null;
    this.differentiationBean.prefPlnName = null;
    this.differentiationBean.prefPlnNum = null;
    this.differentiationBean.pageSize = 10;
    this.differentiationBean.pageNum = 1;
    // this.first =0;
    // this.toquery();
  }
  //添加
  add() {
    this.router.navigate(['/pages/tzb/valuation/differerentiation/differerentiation-add'])
  }
  //新增的回调函数
  addCall(param) {
    this.display = param;
  }
  //详情 
  detail(param) {
    let para = {
      prefPlnNum: param.prefPlnNum,
      prefPlnName: param.prefPlnName,
      effDt: this.commfunc.transDateN(this.effDt),
      invalDt: this.commfunc.transDateN(this.invalDt),
      invalDtStart: this.commfunc.transDateN(this.invalDtStart),
      invalDtEnd: this.commfunc.transDateN(this.invalDtEnd),
      prefSettgWayCode: this.differentiationBean.prefSettgWayCode,
      prefIntRatePln: this.differentiationBean.prefIntRatePln,
      rcrdStCode: this.differentiationBean.rcrdStCode
    }
    this.router.navigate(['/pages/tzb/valuation/differerentiation/differerentiation-query', para])
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.differentiationBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.differentiationBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.differentiationBean.pageSize;
    //调用查询的方法
    this.toquery();
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
