import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
// import { Router } from '@angular/router';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DiffererentiationBean } from './../bean/differerentiation.bean';
// import { IsDiffWay, IsFazhi, IsDifferWay, IsDifferValComp, IsDifferVal } from './../../constant/codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { decimalInteger, decimalInt, specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-differerentiation-add',
  templateUrl: './differerentiation-add.component.html',
  styleUrls: ['./differerentiation-add.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class DiffererentiationAdd implements OnInit {
  differentiationBean: DiffererentiationBean = new DiffererentiationBean();
  date3;
  cars: any = [];
  list: any = [];
  list1: any = [];
  list2: any = [];
  list3: any = [];
  prefDmsnlCmprSymbCode: any;
  cars1: any = [];
  ch: any = CALENDAR_CN;
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //传入详情的值
  inValueCode: string;
  userform: FormGroup; //校验 
  isRed: boolean = false;
  show: boolean = false;
  isRed1: boolean = false;
  show1: boolean = false;
  //提示信息
  msgs: Message[] = [];
  effDt: Date;
  invalDt: Date;
  totalNum: string;
  isDiffWay: any = [];
  isFazhi: any = [];
  isDifferWay: any = [];
  isDifferVal: any = [];
  isDifferValComp: any = [];
  taskCategoryId: string;
  //差异化控制boolean:根据差异化设定方式和差异化方式代码
  isWritePrefWayCode: boolean = true;  //差异化方式代码可输
  isWritePrefIntRatePln: boolean = true; //差异化利率计划可输
  isWritePrefVal: boolean = true; //差异化值可输
  isRequirePrefVal: boolean = false; //差异化值必输
  prefValCondition: string = "";
  isDisable: boolean = false;
  decimalInt = decimalInt;
  decimalInteger = decimalInteger;
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  code: any;//获取所有码值

  constructor(public httpService: ValuationHttpService,
    public commfunc: Commfunc,
    public route: Router,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public fb: FormBuilder
  ) {
    this.codeValues();
    this.isDiffWay = this.code['IsDiffWay'];
    this.isFazhi = this.code['IsFazhi'];
    this.isDifferWay = this.code['IsDifferWay'];
    this.isDifferVal = this.code['IsDifferVal'];
    this.isDifferValComp = this.code['IsDifferValComp'];
    if (this.activatedRoute.snapshot.params.taskCategoryId) {
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId;
    }
  }
  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    // this.prefDmsnlCmprSymbCode = [{ lable: '01', value: '01' }, { lable: '02' }, { value: '02' }];
    this.userform = this.fb.group({
      'prefPlnName': new FormControl('', [Validators.required, Validators.maxLength(200), specialSymbol]),
      'effDt': new FormControl('', Validators.required),
      'invalDt': new FormControl(''),
      'prefSettgWayCode': new FormControl('', Validators.required),
      'prefWayCode': new FormControl(''),
      'prefPrtyRuleLvl': new FormControl('', [Validators.required, Validators.maxLength(250), checkNumber]),
      'prefDegree': new FormControl(''),
      'prefIntRatePln': new FormControl(''),
      'prefVal': new FormControl(''),
      'intRateLmtTpCode': new FormControl(''),
      'hghstRateLmt': new FormControl(''),
      'mnIntLmt': new FormControl(''),
      'txInf': new FormControl('', [Validators.maxLength(250), specialSymbol]),
    });
    this.httpService.queryRatePlanInternalListService({ intRatePlnNum: "", intRatePlnName: "" }).subscribe(data => {
      // let list1Temp: any =[];
      // this.list1 = data.resultList;
      // if (data.resultList.length > 0) {
      //   data.resultList.forEach((item) => {
      //       let obj = {};
      //       obj['label'] = item['intRatePlnName'];
      //       obj['value'] = item;
      //       this.list1.push(obj)
      //     })
      //   }
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "intRatePlnName", "intRatePlnNum");
        this.list1.unshift({ label: '请选择', value: '' })
      }
    });
    this.httpService.queryDimensionMaintListService({ dmslModule: "3", dmsnlPool: "2" }).subscribe(data => {
      this.list2 = data.resultList;
    });

    if (this.differentiationBean.intRateLmtTpCode == undefined) {
      this.isRed = false;
      this.show = true
    } else {
      this.show = false;
      this.isRed = true;
    }

    if (this.differentiationBean.prefSettgWayCode == undefined) {
      this.isRed1 = false;
      this.show1 = true
    } else {
      this.show1 = false;
      this.isRed1 = true;
    };
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //新增维度
  add() {
    let aa = _.clone(this.list);
    //this.list = [];
    aa.push({
      prefDmsnlCode: '', prefDmsnlCmprSymbCode: '', prefDmsnlVal: '', list2: [{ label: '请选择' }, { label: '个人房屋分期抵押', value: '1' }]
    });
    this.list = aa;
  }
  //减少维度
  doReduce(i) {
    let arr = _.clone(this.list);
    arr.splice(i, 1);
    this.list = arr;
  }

  disableText1() {
    this.differentiationBean.prefSettgWayCode = this.differentiationBean.prefSettgWayCode || '';
    let setWayCode = this.differentiationBean.prefSettgWayCode;
    let wayCode = this.differentiationBean.prefWayCode;
    switch (setWayCode) {
      case '':
        this.isWritePrefVal = true;
        this.isWritePrefIntRatePln = true;
        this.isWritePrefWayCode = true;
        this.isRequirePrefVal = false;
        this.differentiationBean.prefIntRatePln = '';
        break;
      case '1':
        this.isWritePrefIntRatePln = false;
        if (wayCode == '0') {
          this.isWritePrefVal = false;
          this.isWritePrefWayCode = true;
          this.isRequirePrefVal = false;
        } else if (wayCode == '1') {
          this.isWritePrefVal = true;
          this.isWritePrefWayCode = true;
          this.isRequirePrefVal = true;
        } else if (wayCode == '2') {
          this.isWritePrefVal = true;
          this.isWritePrefWayCode = true;
          this.isRequirePrefVal = true;
        } else {
          this.isWritePrefVal = true;
          this.isWritePrefWayCode = true;
          this.isRequirePrefVal = false;
        }
        break;
      case '2':
      case '3':
        this.isWritePrefVal = true;
        this.isWritePrefIntRatePln = false;
        this.isWritePrefWayCode = false;
        this.isRequirePrefVal = true;
        break;
      case '4':
        this.isWritePrefVal = false;
        this.isWritePrefIntRatePln = true;
        this.isWritePrefWayCode = false;
        this.isRequirePrefVal = false;
        break;
      default: break;
    }

    if (!this.isWritePrefIntRatePln) {
      this.differentiationBean.prefIntRatePln = '';
    }
    if (!this.isWritePrefWayCode) {
      this.differentiationBean.prefWayCode = '';
    }
    if (!this.isWritePrefVal) {
      this.differentiationBean.prefVal = '';
    }

    this.checkPrefVal(this.differentiationBean.prefVal);
  };

  //校验差异值数据类型
  checkPrefVal(val) {
    if (this.differentiationBean.prefSettgWayCode == '1' && !this.checkDecimal(val)) {
      this.prefValCondition = '1';
    } else if (this.differentiationBean.prefSettgWayCode == '2' && !this.checkZDecimal(val)) {
      this.prefValCondition = '2';
    } else if (this.differentiationBean.prefSettgWayCode == '3' && !this.checkInt(val)) {
      this.prefValCondition = '3';
    } else {
      this.prefValCondition = '';
    }

    if (!val) {
      this.prefValCondition = '';
    }
  }

  //校验上限阈值
  checkInterest() {
    if (this.differentiationBean.hghstRateLmt == '') {
      return false;
    }

    if (this.differentiationBean.intRateLmtTpCode == '1' || this.differentiationBean.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.differentiationBean.hghstRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  checkRate() {
    if (this.differentiationBean.hghstRateLmt == '') {
      return false;
    }

    if (this.differentiationBean.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.differentiationBean.hghstRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  //校验下限阈值
  checkInter() {
    if (this.differentiationBean.mnIntLmt == '') {
      return false;
    }

    if (this.differentiationBean.intRateLmtTpCode == '1' || this.differentiationBean.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.differentiationBean.mnIntLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  checkRa() {
    if (this.differentiationBean.mnIntLmt == '') {
      return false;
    }

    if (this.differentiationBean.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.differentiationBean.mnIntLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  //校验14,7位正负
  checkDecimal(val) {
    var reg = /^[+|-]{0,1}[0-9]{0,7}(\.[0-9]{0,7})?$/;
    return reg.test(val);
  }

  //校验14,7位正
  checkZDecimal(val) {
    var reg = /^[0-9]{0,7}(\.[0-9]{0,7})?$/;
    return reg.test(val);
  }

  //校验7位正负
  checkInt(val) {
    var reg = /^[+|-]{0,1}(\d+){0,7}$/;
    return reg.test(val);
  }

  disableText() {
    this.differentiationBean['hghstRateLmt'] = this.differentiationBean['hghstRateLmt'] || '';
    this.differentiationBean['mnIntLmt'] = this.differentiationBean['mnIntLmt'] || '';
    if (!this.differentiationBean.intRateLmtTpCode
      || this.differentiationBean.intRateLmtTpCode == "9") {
      this.isRed = false;
      this.show = true

      this.differentiationBean.hghstRateLmt = '';
      this.differentiationBean.mnIntLmt = '';
    } else {
      this.isRed = true;
      this.show = false
    }
  };

  //保存
  save() {
    // 校验差异化方式代码
    if (this.differentiationBean.prefSettgWayCode == '1' && (!this.differentiationBean.prefWayCode || this.differentiationBean.prefWayCode == '00')) {
      return;
    }

    if (this.isDisable) {
      return;
    }
    if (this.prefValCondition) {
      return;
    }
    if (this.differentiationBean.prefSettgWayCode == '1' && !this.differentiationBean.prefWayCode) {
      return
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //差异值检查
    if (this.isRequirePrefVal && (this.differentiationBean.prefVal == '' || this.differentiationBean.prefVal == undefined)) {
      return;
    }
    //差异化方式代码非空判断
    if ((this.differentiationBean.prefWayCode == '' || this.differentiationBean.prefWayCode == undefined) && this.differentiationBean.prefSettgWayCode == '1') {
      return;
    }
    //差异化利率计划非空判断
    if ((this.differentiationBean.prefIntRatePln == '' || this.differentiationBean.prefIntRatePln == undefined) && this.differentiationBean.prefSettgWayCode == '4') {
      return;
    }
    //阀值类型下拉控制
    if (this.differentiationBean.intRateLmtTpCode && this.differentiationBean.intRateLmtTpCode != '9') {
      if (!this.differentiationBean.hghstRateLmt || !this.differentiationBean.mnIntLmt) {
        this.isRed = true;
        return;
      }
    }
    //上限校验
    if (this.differentiationBean.intRateLmtTpCode == '1' || this.differentiationBean.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.differentiationBean.hghstRateLmt)) {
        return;
      }
    }
    if (this.differentiationBean.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.differentiationBean.hghstRateLmt)) {
        return;
      }
    }
    //下限校验
    if (this.differentiationBean.intRateLmtTpCode == '1' || this.differentiationBean.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.differentiationBean.mnIntLmt)) {
        return;
      }
    }
    if (this.differentiationBean.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.differentiationBean.mnIntLmt)) {
        return;
      }
    }

    //判断差异化维度是否添加值
    if (this.list.length <= 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '差异化维度至少添加一条' });
      return;
    } else {
      let status: boolean = false;
      this.list.forEach(element => {
        if (!element.prefDmsnlCode
          || !element.prefDmsnlCmprSymbCode
          || !element.prefDmsnlVal) {
          status = true;
          return;
        }
      });
      if (status) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '每条差异化维度不能有空' });
        return;
      }
    }

    //时间戳转为十位
    let aa = this.commfunc.transDateN(this.effDt);//生效日期
    let bb = this.commfunc.transDateN(this.invalDt);
    this.differentiationBean.effDt = aa;
    this.differentiationBean.invalDt = bb;
    this.differentiationBean.prefPlnvluList = this.list;
    delete this.list[0].list2;
    //this.list[0].prefDmsnlVal = this.list[0].prefDmsnlVal.dmsnlValue;
    // this.differentiationBean.prefPlnvluList[0].prefDmsnlVal.dmsnlValue='05' 
    // this.list[0].prefDmsnlVal.dmsnlValue= this.differentiationBean.prefPlnvluList;

    // this.list.dmsnlValue= this.differentiationBean.prefPlnvluList;
    // if(this.differentiationBean.prefPlnvluList)
    let copyDifferentiationBean = _.cloneDeep(this.differentiationBean);
    //去掉不需要的数据
    copyDifferentiationBean.prefPlnvluList.forEach(element => {
      delete element['list3'];
    });
    //处理工作流的参数
    this.differentiationBean['taskCategoryId'] = this.taskCategoryId + '';
    copyDifferentiationBean.taskCategoryId = this.taskCategoryId + ''
    // let para={
    //   copyDifferentiationBean:copyDifferentiationBean,
    //   taskCategoryId:this.differentiationBean['taskCategoryId']
    // }
    this.isDisable = true;
    this.httpService.addRateDifferentRuleService(copyDifferentiationBean).subscribe(data => {
      // this.differentiationBean=data.returnCode;
      if (data.returnCode.code == "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.outValue.emit(false);
        setTimeout(() => {
          let param = {
            prefPlnNum: data.prefPlnNum
          }
          this.route.navigate(['/pages/tzb/valuation/differerentiation', param]);
        }, 1000)

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.isDisable = false;
    })
  }
  //返回
  back() {
    window.history.go(-1)
  }

  doSelect(i, car) {
    this.list3 = [];
    this.httpService.querydetailTmsDicService({ dmsnlCode: this.list[i]['prefDmsnlCode'] }).subscribe(data => {
      this.list[i]['list2'] = data.resultList;
      // this.list.prefDmsnlVal=this.list[i]['list2'];
      let temp = [];
      if (data.resultList && data.resultList.length > 0) {
        data.resultList.forEach(item => {
          let obj = {};
          obj['label'] = item.dmslValDesc;
          obj['value'] = item.dmsnlValue;
          this.list3.push(obj)
        })
        this.list[i]['list3'] = this.list3;
      }
      // temp = _.clone(this.list3)
      // this.list3 = _.clone(temp)
    });
    car.prefDmsnlVal = '';
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}