import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { Differerentiation } from "./differerentiation.component";
import { DiffererentiationAdd } from "./differerentiation-add/differerentiation-add.component";
import { DiffererentiationDetailAddComponent } from "./differerentiation-detail-add/differerentiation-detail-add.component";
import { DiffererentiationQueryComponent } from "./differerentiation-query/differerentiation-query.component";
// import { DiffererentiationQueryDetailComponent } from "./differerentiation-query-detail/differerentiation-query-detail.component";


export const routes: Routes = [
    {
        path: '',component: Differerentiation,
    },  
    {path:'differerentiation-add',component:DiffererentiationAdd},
    {path:'differerentiation-detail-add',component:DiffererentiationDetailAddComponent},
    {path:'differerentiation-query',component:DiffererentiationQueryComponent},
    // {path:'differerentiation-query-detail',component:DiffererentiationQueryDetailComponent}
];



export const DiffererentiationRouting: ModuleWithProviders = RouterModule.forChild(routes);
