import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { DiffererentiationBean } from './../bean/differerentiation.bean';
import { Message } from 'primeng/primeng';
import { ActivatedRoute, Params } from '@angular/router';
import { Router } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
// import { IsRcord,IsDiffWay } from './../../constant/codeValue'
@Component({
  selector: 'app-differerentiation-query',
  templateUrl: './differerentiation-query.component.html',
  styleUrls: ['./differerentiation-query.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class DiffererentiationQueryComponent implements OnInit, OnChanges {
  differentiationBean: DiffererentiationBean = new DiffererentiationBean();
  aa: any;
  isRcord: any = [];
  isDiffWay: any = [];
  code: any;//获取所有码值
  cars: any = [];
  //table数据
  list: any = [];
  //传入详情的值
  inValueCode: string;
  total: number;
  first: number = 0;//分页控制
  totalNum: string;
  //模态框
  display: boolean = false;
  //1详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //提示信息
  msgs: Message[] = [];
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  constructor(public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public activeRoute: ActivatedRoute,
    public router: Router,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.isDiffWay = this.code['IsDiffWay'];
    this.isRcord = this.code['IsRcord'];
  }
  ngOnInit() {
    this.aa = this.activeRoute.snapshot.params['prefPlnName'];
    this.differentiationBean.pageSize = 10;
    this.differentiationBean.pageNum = 1;
    this.query()
  }
  ngOnChanges() {
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //查询方法
  query() {
    let param = this.activeRoute.params['value'];
    let subParam = {
      pageSize: this.differentiationBean.pageSize,
      pageNum: this.differentiationBean.pageNum
    };
    for (let item in param) {
      subParam[item] = param[item]
    }
    this.httpService.queryRateDifferentRuleByCodeService(subParam).subscribe(data => {
      this.list = data.resultList;
      this.total = data.total;
    }, err => {
    })
  }
  //返回
  goback() {
    window.history.go(-1);
  }
  //新增
  add(param) {
    this.router.navigate(['/pages/tzb/valuation/differerentiation/differerentiation-detail-add', { prefPlnName: this.aa }])
  }
  //新增的回调方法
  addCall(param) {
    this.display = param;
  }
  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }
  //详情
  detail(param) {
    this.headerTitle = '利率差异化规则-详情';
    this.showModel = '2';
    this.display = true;
    this.inValueCode = JSON.stringify(param);
    setTimeout(() => {
      this.inValueCode = "";
    }, 2000)

  }
  //修改的回掉函数
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    }
    this.query()
  }
  //修改
  update(param) {
    this.headerTitle = '利率差异化规则-修改';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param;
    setTimeout(() => {
      this.inValueCode = "";
    }, 2000)
  }
  //删除
  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          prefPlnNum: car.prefPlnNum,
          prefRuleNum: car.prefRuleNum,
          effDt: car.effDt
        };
        this.httpService.deleteRateDifferentRuleService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
      }
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.differentiationBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.differentiationBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.differentiationBean.pageSize;
    //调用查询的方法
    this.query();
    // this.beanValue.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
